import { buildMockModuleProps, IImageData} from '@msdyn365-commerce/core';
/// <reference types="jest" />

// tslint:disable-next-line:no-unused-variable
import { render } from 'enzyme';
import * as React from 'react';
import SocialShareItem from '../social-share-item';
import { ISocialShareItemConfig, ISocialShareItemProps, socialMedia } from '../social-share-item.props.autogenerated';
import SocialShareItemView from '../social-share-item.view';

const mockConfig: ISocialShareItemConfig = {
  socialMedia: socialMedia.facebook,
  className: '',
  icon: {} as IImageData,
};

const mockConfig1: ISocialShareItemConfig = {
  socialMedia: socialMedia.linkedin,
  icon: {} as IImageData,
  className: 'ms-accordion-item-content'
};

const myReactNodeChild1: React.ReactNode = 'whatever child module 1';
const slots = {
  accordionItemContent: [myReactNodeChild1]
        };

describe('SocialShareItem', () => {
  it('renders correctly', () => {
    let moduleProps: ISocialShareItemProps<ISocialShareItemConfig>;
    moduleProps = {
      slots: slots,
       ...buildMockModuleProps({}, mockConfig) as ISocialShareItemProps<ISocialShareItemConfig>,
       // @ts-ignore
       renderView: SocialShareItemView
    };
    const component = render(<SocialShareItem {...moduleProps} />);
    expect(component).toMatchSnapshot();
  });
});

describe('SocialShareItemWithCustomeURL', () => {
  it('renders correctly', () => {
    let moduleProps: ISocialShareItemProps<ISocialShareItemConfig>;
    moduleProps = {
      slots: slots,
       ...buildMockModuleProps({}, mockConfig1) as ISocialShareItemProps<ISocialShareItemConfig>,
       // @ts-ignore
       renderView: SocialShareItemView
    };
    const component = render(<SocialShareItem {...moduleProps} />);
    expect(component).toMatchSnapshot();
  });
});
